.accordion-button {

display: none;


    @include vp-767 {
      display: block;
        position: absolute;

        top: 50%;
        transform: translateY(-50%);
        right: 0;
  margin: 0;
  padding: 0;
  border: 0;
  width: 40px;
  height: 40px;
  background-color: transparent;

  &::before,
  &::after {
    content: "";
    position: absolute;

    // right: 0;

    background-color: $color-default-white;
  }

  // @media (min-width: 768px) {
  //   display: none;
  // }

  &--closed {
    &::before {
      width: 15px;
      height: 1px;
      top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    }

    &::after {
      width: 15px;
      height: 1px;
      top: 50%;
    left: 50%;
      transform: translate(-50%, -50%) rotate(90deg);
    }
  }

  &--open {
    &::before {
      width: 15px;
      height: 1px;
      top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    }

    &::after {
      content: none;
    }
  }
  }
}
